import React, { useState } from 'react'
import arrow2 from '../media/arrow2.svg'
import nortonLogo from '../media/norton-secured.png'
export const Footer = props => {

  return (
    <footer className="footer os bold">
      <div className="menu-container">
        <div className="menu-item">
          <img src={arrow2} alt="arrow" />
          <a href="#">Demo</a>
        </div>
        <div className="menu-item">
          <img src={arrow2} alt="arrow" />
          <a href="#">Vezi aici mai multe detalii</a>
        </div>
        <div className="menu-item">
          <img src={arrow2} alt="arrow" />
          <a href="#">Manual de utilizare</a>
        </div>
      </div>
      <div className="info-container">
        <div className="info-logo-container">
          <img src={nortonLogo} alt="norton-logo" />
        </div>
        <div className="info-content os">
        Ai remarcat informatiile de securitate despre acest site? 
        Butonul de identitate este verde si imaginea arata de asemenea un lacat inchis - acestea indica legitimitatea site-ului pe care te afli ca o pagina BCR. 
        Te rugam sa nu introduci Numele de utilizator sau parola in nicio pagina ce nu va afisa acest text informativ si care nu va respecta precizarile de securitate. 
        <span><a  href="#"> Apasa aici</a></span> pentru mai multe informatii.
        </div>
      </div>
    </footer>
  )
}