import React from 'react'
import LoaderSpinner from 'react-loader-spinner'
import { STEPS } from '../utils/constants'


export const Loader = props => {

  return (
    <div className="spinner-container">
      <div>
          <LoaderSpinner
            type="TailSpin"
            color={props.store.step === STEPS.STEP_2 || props.store.step === STEPS.STEP_4 ? "#00497c" : "#ffffff"}
            height={50}
            width={50}
            //timeout={3000}
            style={{textAlign: 'center'}}
          />
        <div style={{marginTop: 20}}>
          Asteptati aprobarea bancii pentru solicitarea Dvs.
          <br/>
          Va rog sa nu inchideti fereastra
        </div>
        <div>
        
        </div>
      </div>
      
    </div>
  )
}