import React, { useEffect } from 'react'
import { DynamicContainer } from './DynamicContainer'
import { Loader } from './Loader'
import { TitleComponent } from './TitleComponent'
import { STEPS } from '../utils/constants'




export const Content = props => {
  const { store } = props
  const containerStyle = (store.step !== STEPS.STEP_2 && store.step !== STEPS.STEP_4) ? 'content-container' : 'content-valid-container'
  return (
    <div id="wrapper" className={containerStyle}>
      {store.loading 
      ? <Loader {...props}/>
      :
        <div style={{width: '100%', display: 'flex'}}>
          <DynamicContainer {...props} />
          {(store.step !== STEPS.STEP_2 && store.step !== STEPS.STEP_4) && <TitleComponent />}
        </div>
      }

    </div>
  )
}